.header {
    width: 100%;
    height: 10%;
    display: flex;
    transition: all 0.6s ease;
    .logo-container {
        width: 80%;
        .logo {
            width: 100px;
            height: 100px;
        }
    }
    .option-container {
        width: 20%;
        float: left;
        display: flex;
        flex-direction: row;
        padding: 2px 10px;
        margin-top: 5%;
        .option{
            padding: 0px 14px;
            align-items: center;
            justify-content:space-between;
            .link{
                color: black; font-size: larger;
                cursor: pointer;
            }
        }
    }
 
}

