.menu-item {
    margin: 0 7.5px 15px;
    min-width: 20%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    overflow: hidden;
    transition: all 0.6s ease;
    &:hover {
        cursor: pointer;

        & .background-img {
            transform: scale(1.1);
            transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }

        & .content {
            opacity: 0.9;
        }
    }

    &:first-child {
        margin-right: 7.5px;
    }

    &:last-child {
        margin-left: 7.5px;
    }
    &.large {
        height: 400px;
    }
    .background-img {
        width: 100%;
        height: 100%;
    }
    .content {
        height: 90px;
        padding: 0 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;
        background-color: rgb(228, 228, 228);
        position: absolute;
        transition: all 0.6s ease;
      
        .title {
            font-weight: bolder;
            margin-bottom: 6px;
            font-size: 22px;
            color: darkslategray;
        }

        .sub-title {
            font-weight: lighter;
            font-size: 16px;
        }
    }
}
